<template>
  <div class="mainStream">
    <!--
      <button id="btn_comments" type="button" class="btn" @click="login">授權</button>
      <button type="button" class="btn">篩選</button>
      <button type="button" class="btn time"></button>
    -->
    <List :filter="{}" @focus="focus"></List>
  </div>
</template>

<script>
  var source;
  import List from './List.vue';
  import {
    mapState,
    mapMutations,
  } from 'vuex';
  export default {
    data() {
      return {
        event_start: false
      }
    },
    computed: {
      ...mapState({
        lists: 'lists',
        video: 'video',
        export: 'export',
        uid: 'uid',
      }),
      filterID() {
        return (this.lists[0]) ? parseInt(this.lists[0].id, 10) + 1 : 0;
      }
    },
    components: {
      List,
    },
    methods: {
      stopEvent() {
        source.close();
      },
      startEvent() {
        if (!this.event_start){
          this.event_start = true;
                  let url =
          `https://streaming-graph.facebook.com/${this.video.id}/live_comments?access_token=${this.video.at}&comment_rate=one_per_two_seconds&fields=id,from,message,message_tags,created_time,attachment`;
        source = new EventSource(url);
        source.onopen = function (event) {
          console.log(event);
          console.log('open');
        };
        source.onmessage = function (event) {
          var data = event.data;
          this.$store.commit('setRAW', JSON.parse(data));
          console.log(data);
        }.bind(this);
        source.onerror = function (event) {
          console.log(event);
          console.log('close');
        };
        //{"view_id":48052963,"id":"1697214670391581_1697220760390972","message":"f","from":{"id":"429357370510657","name":"我要試頁籤～～"},"created_time":"2018-03-20T05:08:24+0000"}
        }
      },
      
      focus(tar) {
        let condition = {
          type: 'from',
          filter: tar,
          id: this.filterID,
        }
        this.$store.commit('addFilter', condition);
      },
    },
  }

</script>

<style lang="scss" scoped>
  a {
    text-decoration: none;
  }

  .btn {
    min-width: 110px;
    height: 30px;
    text-align: center;
    line-height: 30px;
    padding: initial;
    margin-right: 5px;
    border-radius: 3px;
    color: #FFF;
    font-size: 16px;
    background: #44b1da;
    display: inline-block;
    margin-bottom: 0;
    white-space: nowrap;
    vertical-align: middle;
    cursor: pointer;
    border: 1px solid transparent;
    user-select: none;
    &.time {
      background: #50c350;
      &:before {
        content: '開始記錄';
        color: #FFF;
      }
      &.end {
        background: #F00;
        &:before {
          content: '結束記錄';
          color: #FFF;
        }
      }
    }
  }

  .index {
    .mainStream {
      height: 100%;
    }
  }

</style>
