<template>
  <div id="app">
    <Login :class="{'fadeout':logged}" @complete="login"></Login>
    <div class="wrapper">
      <header>Facebook直播觀察小助手</header>
      <FilterConsole></FilterConsole>
      <div class="index">
        <MainStream ref="MainStream"></MainStream>
        <div class="filterStream">
          <List v-for="li in lists" :key="li.id" :filter="li" @focus="focus"></List>
        </div>
      </div>

    </div>
  </div>
</template>

<script>
  console.clear();
  var moment = require('moment');
  import MainStream from './MainStream.vue';
  import List from './List.vue';
  import Login from './Login.vue';
  import FilterConsole from './FilterConsole.vue';
  import {
    mapState,
    mapMutations,
  } from 'vuex';

  export default {
    data() {
      return {
        moment,
        logged: false,
        error: false,
        btn_end: false,
      }
    },
    computed: {
      ...mapState({
        raw: 'raw',
        lists: 'lists',
        video: 'video',
      }),
      filterID() {
        return (this.lists[0]) ? parseInt(this.lists[0].id, 10) + 1 : 0;
      }
    },
    components: {
      Login,
      MainStream,
      List,
      FilterConsole,
    },
    mounted() {
      window.fbAsyncInit = function () {
        FB.init({
          appId: '617357865113012',
          cookie: true,
          xfbml: true,
          version: 'v2.11'
        });
        FB.AppEvents.logPageView();
      };
      // dev 617357865113012
      // pro 2103480793256333 1494465264176752 160833824415744
      window.onerror = handleErr;
      let vm = this;

      function handleErr(msg, url, l) {
        vm.error = true;
      }
    },
    methods: {
      login() {
        this.logged = true;
        let exist_data = [];
        let vm = this;
        finish();
        // return false;
        //{"view_id":48052963,"id":"1697214670391581_1697220760390972","message":"f","from":{"id":"429357370510657","name":"我要試頁籤～～"},"created_time":"2018-03-20T05:08:24+0000"}
        FB.api(
          `/${this.video.id}/comments?limit=500&access_token=${this.video.at}&fields=id,from,message,message_tags,created_time,attachment`,
          res => {
            console.log(res);
            if (res.data.length > 0) {
              exist_data = res.data;
              if (res.paging.next) {
                next(res.paging.next);
              } else {
                finish();
              }
            } else {
              finish();
            }
          });

        function next(url) {
          axios.get(url).then(r => {
            let res = r.data;
            if (res.data.length > 0) {
              exist_data = exist_data.concat(res.data);
              if (res.paging.next) {
                next(res.paging.next);
              } else {
                finish();
              }
            } else {
              finish();
            }
          })
        }

        function finish() {
          for(let i of exist_data){
            if (!i.from){
              i.from = {
                id: '',
                name: 'No Auth',
              }
            }
          }
          vm.$store.commit('replaceRAW', exist_data);
          vm.$refs.MainStream.startEvent();
        }
      },

      focus(tar) {
        let condition = {
          type: 'from',
          filter: tar,
          id: this.filterID,
        }
        this.$store.commit('addFilter', condition);
      },
    },

  }

</script>

