  a {
    text-decoration: none;
  }
  .index {
    flex-grow: 1;
    height: calc(100vh - 50px - 64.5px);
    overflow: auto;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-content: flex-start;
    align-items: flex-start;
    .filterStream {
      flex-grow: 1;
      display: flex;
      flex-direction: row;
      flex-wrap: nowrap;
      justify-content: flex-start;
      align-content: flex-start;
      align-items: flex-start;
      height: 100%;
    }
  }

</style>
