<template>
  <div>
    <div class="filterConsole">
      <div class="block keyword_block">
        <input class="keyword" type="text" v-model="keyword" placeholder="請輸入要觀察的關鍵字" />
        <button class="btn btn-success" @click="keywordFilter">篩選關鍵字</button>
      </div>
      <div class="block time_block">
        <button class="btn btn-success" @click="timeFilter()">時間篩選</button>
      </div>
      <!-- <div class="block comment_block">
        <input class="comment" type="text" v-model="comment" placeholder="請輸入留言" />
        <button class="btn" :class="{'btn-default':!this.comment_timestamp, 'btn-warning': this.comment_timestamp}" @click="add_timestamp">
          <icon name="clock-o"></icon>
        </button>
        <button class="btn btn-primary" @click="send_comment">發送留言</button>
      </div> -->
      <div class="block">
        <!-- <button class="btn btn-success" @click="share">匯出資料{{shareURL}}</button>
        <button class="btn btn-success" @click="fakeMess">生成資料</button> -->
        <button class="btn btn-success" @click="makePoll">發起投票</button>
      </div>
    </div>
  </div>
</template>

<script>
  import {
    mapState,
    mapMutations
  } from 'vuex';

  export default {
    data() {
      return {
        keyword: '',
        comment: '',
        comment_timestamp: false,
        timeFilterID: '',
        shareURL: '',
      }
    },
    computed: {
      ...mapState({
        lists: 'lists',
        video: 'video',
        export: 'export',
      }),
      filterID() {
        return (this.lists[0]) ? parseInt(this.lists[0].id, 10) + 1 : 0;
      },
    },
    methods: {
      keywordFilter() {
        let condition = {
          type: 'message',
          filter: this.keyword,
          id: this.filterID,
        }
        this.$store.commit('addFilter', condition);
        this.keyword = '';
      },
      timeFilter(timestart = new Date()) {
        let condition = {
          type: 'time',
          filter: {
            start: timestart,
            end: ''
          },
          id: this.filterID,
        }
        this.$store.commit('addFilter', condition);
        return condition.id;
      },
      send_comment() {
        if (this.comment !== '') {
          let obj = {
            message: this.comment
          }
          FB.api(`/${this.video.video}/comments?fields=created_time&access_token=${this.video.at}`, 'POST', obj,
            function (res) {
              if (this.comment_timestamp === true) {
                if (this.timeFilterID === '') {
                  this.timeFilterID = this.timeFilter(res.created_time);
                } else {
                  let obj = {
                    end: res.created_time,
                    id: this.timeFilterID,
                  };
                  this.$store.commit('stopTime', obj);
                  this.timeFilterID = '';
                }
              }
              this.comment_timestamp = false;
              this.comment = '';
            }.bind(this));
        }
      },
      add_timestamp() {
        this.comment_timestamp = !this.comment_timestamp;
      },
      makePoll(){
        let obj = {
          question: 'This is a test',
          options: ['選項A','選項B','選項C','選項D'],
          show_results: true,
        }
        FB.api(`/${this.video.video}/polls?access_token=${this.video.at}`, 'POST', obj, function(res){
          console.log(res);
        });
      },
      share() {
        // let url = `${location.href}?liveid=${this.video.id}&videoid=${this.video.video}&at=${this.video.at}`;
        // console.log(url);
        let provider = new firebase.auth.FacebookAuthProvider();
        provider.setCustomParameters({
          'display': 'popup'
        });
        firebase.auth().signInWithPopup(provider).then(res => {
          console.log(res);
          let uid = res.user.uid;
          this.shareURL = res.user.uid;
          let obj = {
            uid: res.user.uid,
          }
          this.$store.commit('setExport', obj);

          firebase.database().ref(`users/${uid}`).set({
            email: res.additionalUserInfo.profile.email,
            name: res.additionalUserInfo.profile.name,
            fbid: res.additionalUserInfo.profile.id,
            at: res.credential.accessToken,
          }).catch(error => {
            console.error("寫入使用者資訊錯誤", error);
          }).then(() => {
            console.log('寫入完成');
          });
        })
      },
      genMess() {
        let mess = Math.floor(Math.random() * 1000);
        let users = [{
          id: '100000075678363',
          name: '泰迪黃'
        }, {
          id: '1009468269086007',
          name: '粉絲團留言抽籤小助手'
        }, {
          id: '429357370510657',
          name: 'live video'
        }];
        let obj =
          `{"view_id":48052963,"id":"1697214670391581_1697220760390972","message":"${mess}","from":${JSON.stringify(users[(mess%3)])},"created_time":"${new Date()}"}`;
        this.$store.commit('setRAW', JSON.parse(obj));
        if (this.export.uid) {
          let postRef = firebase.database().ref(`/posts/${this.export.uid}`);
          postRef.push().set({
            num: obj
          }).then(() => {
            console.log('新增成功');
          });
        }
      },
      fakeMess(){
        setInterval(this.genMess, 2000);
      },
    }
  }

</script>

<style lang="scss" scope>
  .filterConsole {
    background: #FFF;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-content: center;
    align-items: center;
    .block {
      padding: 15px 20px;
      border-right: 1px solid #ccc;
      border-bottom: 1px solid #ccc;
      &.comment_block {
        .btn {
          svg {
            vertical-align: middle;
            margin-top: -3px;
          }
        }
        .btn-warning {
          svg {
            color: #F00;
          }
        }
      }
      .keyword {
        vertical-align: middle;
        padding: 0 10px;
      }
      .comment {
        vertical-align: middle;
        padding: 0 10px;
        width: 300px;
      }
    }
  }

</style>
