<template>
  <div class="cover">
    <div class="login">
      <!-- <input type="text" v-model="uid">
      <button @click='client_login'>client</button> -->
      <button v-if="!options.length" class="btn" @click="login">登入</button>
      <div v-if="options.length">
        <select v-model="live_place">
          <option value="">請選擇直播的地方：</option>
          <option v-for="opt in options" :value="opt.fbid">{{opt.name}}</option>
        </select>
        <button class="btn getVideo" @click="getVideo">確定</button>
      </div>
    </div>
  </div>
</template>

<script>
  import {
    mapState,
    mapMutations,
  } from 'vuex';

  export default {
    data() {
      return {
        at: [],
        options: [],
        live_place: '',
        uid: 'XRcucMAiShekUQujmX9BXoA02i12',
      }
    },
    computed: {
      ...mapState({
        video: 'video',
      }),
    },
    methods: {
      login() {
        FB.login(response => {
          this.callback(response);
        }, {
          scope: 'manage_pages,pages_show_list,publish_pages',
          return_scopes: true
        });
      },
      callback(response) {
        // console.log(response);
        // this.user.id = response.authResponse.userID;
        if (response.status === 'connected') {
          console.log(response);
          let obj = {
            id: response.authResponse.userID,
            at: response.authResponse.accessToken,
          }
          this.at.push(obj);

          // this.getMe().then(res => {
          //   let option = {
          //     fbid: res.id,
          //     name: res.name
          //   }
          //   this.options.push(option);

            
          // });
          this.getPage().then(res2 => {
            for (let i of res2) {
              let obj = {
                id: i.id,
                at: i.access_token,
              }
              this.at.push(obj);

              let option = {
                fbid: i.id,
                name: i.name
              }
              this.options.push(option);
            }
          });
        }
      },
      getMe() {
        return new Promise((resolve, reject) => {
          FB.api(`/me`, (res) => {
            resolve(res);
          });
        });
      },
      getPage() {
        return new Promise((resolve, reject) => {
          FB.api(`/me/accounts?limit=100`, (res) => {
            resolve(res.data);
          });
        });
      },
      getVideo() {
        if (this.live_place === '') {
          alert('請在下拉選單選擇直播的地方');
        } else {
          let target = {};
          this.at.forEach(item => {
            if (item.id === this.live_place) {
              target.at = item.at;
            }
          })
          FB.api(`${this.live_place}/live_videos?fields=video,status`, res => {
            for (let i of res.data) {
              if (i.status === 'LIVE') {
                target.id = i.id;
                target.video = i.video.id;
              }
            }
            if (target.id) {
              this.$store.commit('setVideo', target);
              this.$emit('complete');
            } else {
              // alert('找不到正在直播的影片');
              target.id = '802029246653130';
              this.$store.commit('setVideo', target);
              this.$emit('complete');
            }
          });
        }
      },
      client_login() {
        this.$store.commit('setClient', this.uid);
        let vm = this;
        firebase.database().ref(`/posts/${this.uid}`).on('value', snapshot => {
          console.log(snapshot.val());
          let obj = [];
          let res = snapshot.val();
          if (res) {
            Object.keys(res).forEach(item => {
              obj.push(JSON.parse(res[item].num));
            });
          }
          vm.$store.commit('replaceRAW', obj);
        });
        this.$emit('complete');
      }
    }
  }

</script>

<style lang="scss" scoped>
  .cover {
    position: fixed;
    width: 100vw;
    height: 100vh;
    left: 0;
    top: 0;
    background: #FFF;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-content: center;
    align-items: center;
    z-index: 100;
    transition: all .2s;
    &.fadeout {
      top: -100vh;
    }
    .login {
      width: 400px;
      height: 200px;
      border: 1px solid #CCC;
      margin: 150px auto;
      display: flex;
      flex-direction: column;
      flex-wrap: nowrap;
      justify-content: center;
      align-content: center;
      align-items: center;
      &>div {
        display: flex;
        flex-direction: column;
        flex-wrap: nowrap;
        justify-content: center;
        align-content: center;
        align-items: center;
      }
    }
  }

  .btn {
    min-width: 110px;
    height: 30px;
    text-align: center;
    line-height: 30px;
    padding: initial;
    margin-right: 5px;
    border-radius: 3px;
    color: #FFF;
    font-size: 16px;
    background: #44b1da;
    display: block;
    margin-bottom: 0;
    white-space: nowrap;
    vertical-align: middle;
    cursor: pointer;
    border: 1px solid transparent;
    user-select: none;
    &.getVideo {
      margin-top: 20px;
    }
  }

</style>
