import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    raw: [],
    lists: [],
    video: {},
    time_record: false,
    export: {},
    uid: '',
  },
  mutations: {
    setRAW(state, data) {
      state.raw.unshift(data);
    },
    replaceRAW(state, data){
      state.raw = data;
    },
    addFilter(state, data){
      state.lists.unshift(data);
    },
    removeFilter(state, index){
      state.lists.splice(index, 1);
    },
    stopTime(state, data){
      state.lists.forEach(item =>{
        if(item.id === data.id){
          item.filter.end = data.end;
        }
      });
    },
    setVideo(state, data){
      state.video = data;
    },
    setExport(state, data){
      state.export = data;
    },
    setClient(state, uid){
      state.uid = uid;
    }
  }
});

export default store;
