<template>
  <div class="list">
    <div class="title" v-if="filter.type">
      <div>
        ({{filterData.length}})
        <icon :name="filterIcon"></icon>
        <div class="badge badge-success" v-if="(filter.type === 'time')" @click.self='editTime(false)'>
          {{filterTitle}}
          <div v-if="timeEdit" class='timeEditor'>
            <input type='text' pos='s' :value='this.filter.filter.start | datetime' @input='setTime'> |
            <input type='text' pos='e' :value='this.filter.filter.end | datetime' @input='setTime'>
            <button class="btn btn-primary" @click.self='editTime(true)'>修改時間</button>
          </div>
        </div>
        <span v-else>{{filterTitle}}</span>
      </div>
      <span class="unfocus" @click="unfocus()" v-if="filter.type">X</span>
    </div>
    <div class="sort">
      <span class="word">排序依據：</span>
      <span class="badge badge-light" :class="{'active':time_active}" @click="sort('time', time_active)">時間</span>
      <label v-if="!(filter.type === 'from')">
        <input type="checkbox" v-model="unique">移除重複</label>
    </div>
    <div class="stop_btn" v-if="filter.type === 'time'">
      <button v-if="!this.filter.filter.end" @click="stopTime">停止紀錄</button>
    </div>
    <div class="lottery" v-if="!(filter.type === 'from')">
      <div class="console">
        <input type="number" placeholder="人數" v-model="people">
        <button class="btn btn-success" @click="draw">抽獎</button>
      </div>
      <ul v-if="winner.length>0">
        <li v-for="li in winner" class="winner">
          <div class="from">
            <p class="name">
              <a :href="li.from.id | fromURL" target="_blank">{{li.from.name}}</a>
            </p>
            <p class="time">{{moment(li.created_time) | time}}</p>
          </div>
          <div class="message">
            <a :href="li.id | fromURL" target="_blank">{{li.message}}</a>
          </div>
        </li>
      </ul>
    </div>
    <ul>
      <li v-for="li in sortData" :class="{'filter_li': filter.type}">
        <button class="focus" @click="focus(li.from)">+</button>
        <div class="from">
          <p class="name">
            <a :href="li.from.id | fromURL" target="_blank">{{li.from.name}}</a>
          </p>
          <p class="time">{{moment(li.created_time) | time}}</p>
        </div>
        <div class="message">
          <a :href="li.id | fromURL" target="_blank">{{li.message}}</a>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
  var moment = require('moment');
  import lodash from 'lodash';
  import {
    mapState,
    mapMutations,
  } from 'vuex';
  export default {
    data() {
      return {
        moment,
        sortby: 'time',
        orderby: true, // true = desc, false = asc
        time_active: true,
        timeEdit: false,
        startTime: '',
        endTime: '',
        people: '',
        winner: [],
        unique: false,
      }
    },
    props: ['id', 'filter'],
    filters: {
      time(val) {
        return moment(val).format('HH:mm:ss');
      },
      datetime(val) {
        return moment(val).format('YYYY-MM-DD HH:mm:ss');
      },
      fromURL(id) {
        return "http://www.facebook.com/" + id;
      },
    },
    computed: {
      ...mapState({
        raw: 'raw',
        lists: 'lists',
      }),
      uniqueData() {
        if (this.unique === true) {
          return _.uniqBy(this.raw, function (e) {
            return e.from.id;
          });
        } else {
          return this.raw;
        }
      },
      filterData() {
        if (this.filter.type === 'message') {
          return this.uniqueData.filter(item => {
            return item.message.indexOf(this.filter.filter) >= 0;
          });
        } else if (this.filter.type === 'from') {
          return this.uniqueData.filter(item => {
            return item.from.id == this.filter.filter.id;
          });
        } else if (this.filter.type === 'time') {
          let end = (this.filter.filter.end == '') ? moment('2100-01-01') : moment(this.filter.filter.end);
          return this.uniqueData.filter(item => {
            return (moment(item.created_time) >= moment(this.filter.filter.start) && moment(item.created_time) <=
              end);
          });
        } else {
          return this.uniqueData.slice(0,1000);
        }
      },
      filterTitle() {
        if (this.filter.type === 'message') {
          return this.filter.filter;
        }
        if (this.filter.type === 'from') {
          return this.filter.filter.name;
        }
        if (this.filter.type === 'time') {
          return `${moment(this.filter.filter.start).format('HH:mm:ss')} ~ ${moment(this.filter.filter.end).format('HH:mm:ss')}`;
        }
      },
      filterIcon() {
        if (this.filter.type === 'message') {
          return 'commenting-o';
        }
        if (this.filter.type === 'from') {
          return 'user-o';
        }
        if (this.filter.type === 'time') {
          return 'clock-o';
        }
      },
      sortData() {
        if (this.sortby === 'time') {
          return this.filterData.sort((a, b) => {
            if (this.orderby) {
              return moment(b.created_time) - moment(a.created_time);
            } else {
              return moment(a.created_time) - moment(b.created_time);
            }
          });
        }
        // if (this.sortby === 'asc') {
        //   return this.filterData.sort((a, b) => {
        //     return a.message - b.message;
        //   });
        // }
        // if (this.sortby === 'desc') {
        //   return this.filterData.sort((a, b) => {
        //     return b.message - a.message;
        //   });
        // }
      }
    },
    methods: {
      setTime(event) {
        if (event.target.attributes.pos.value == 's') {
          this.startTime = event.target.value;
        } else {
          this.endTime = event.target.value;
        }
      },
      focus(tar) {
        this.$emit('focus', tar);
      },
      unfocus() {
        this.lists.forEach((item, index) => {
          if (item.id == this.filter.id) {
            this.$store.commit('removeFilter', index);
          }
        });
      },
      stopTime() {
        this.filter.filter.end = new Date();
      },
      sort(target, order) {
        this.sortby = target;
        this.orderby = !order;
        this.time_active = !this.time_active;
      },
      editTime(open = false) {
        if (open === false) {
          this.timeEdit = true;
          this.startTime = this.filter.filter.start;
        } else {
          this.timeEdit = false;
          if (moment(this.startTime)._isValid) {
            this.filter.filter.start = moment(this.startTime);
          }
          if (moment(this.endTime)._isValid) {
            this.filter.filter.end = moment(this.endTime);
          }
        }
      },
      draw() {
        let raw = _.clone(this.filterData, true);
        raw = shuffle(raw);
        this.winner = raw.slice(0, this.people);

        function shuffle(arr) {
          var i,
            j,
            temp;
          for (i = arr.length - 1; i > 0; i--) {
            j = Math.floor(Math.random() * (i + 1));
            temp = arr[i];
            arr[i] = arr[j];
            arr[j] = temp;
          }
          return arr;
        };
      }
    }
  }

</script>

<style lang="scss" scoped>
  .fa-icon {
    vertical-align: middle;
    margin-right: 5px;
    margin-top: -3px;
    margin-left: 5px;
  }

  .sort {
    padding: 5px 10px;
    background: #F19953;
    .word {
      font-size: 14px;
      color: #FFF;
    }
    .badge {
      cursor: pointer;
      &.active {
        color: #FFF;
        background: #17a2b8;
      }
    }
    label {
      margin-left: 15px;
      color: #FFF;
      font-weight: bold;
      input {
        margin-right: 5px;
        vertical-align: middle;
      }
    }
  }

  .lottery {
    background: #8fa3d2;
    .console {
      padding: 10px;
      input {
        width: 60px;
      }
    }
    ul {
      li.winner {
        background: #ffe0e0;
        border-color: #dc6eb4;
      }
    }
  }

  .list {
    width: 350px;
    height: 100%;
    overflow: auto;
    padding: 10px 15px;
    .title {
      width: 100%;
      background: #3B5FB2;
      color: #FFF;
      padding: 10px;
      display: flex;
      flex-direction: row;
      flex-wrap: nowrap;
      justify-content: space-around;
      align-content: center;
      align-items: center;
      position: relative;
      .unfocus {
        width: 25px;
        height: 25px;
        font-size: 16px;
        border-radius: 50%;
        background: #F45B69;
        color: #FFF;
        border: 0;
        text-align: center;
        cursor: pointer;
      }
      .badge {
        font-size: 100%;
        cursor: pointer;
        position: relative;
        .timeEditor {
          position: absolute;
          margin-left: -20px;
          margin-top: 10px;
          z-index: 5;
          background: #FFF;
          color: #000;
          padding: 0 20px 10px;
          cursor: default;
          input {
            display: block;
            margin: 10px 0;
            width: 150px;
            text-align: center;
          }
        }
      }
    }
    .stop_btn {
      button {
        border: 0;
        width: 100%;
        text-align: center;
        padding: 5px 0;
        background: #de6262;
        cursor: pointer;
        color: #FFF;
      }
    }
    ul {
      padding-left: 0;
      li {
        background: #FFF;
        margin-bottom: 5px;
        padding: 15px 10px 15px 30px;
        border-right: 5px solid #57b757;
        position: relative;
        &.filter_li {
          background: #faffd2;
        }
        .from {
          height: 15px;
          flex-grow: 1;
          display: flex;
          flex-direction: row;
          flex-wrap: nowrap;
          justify-content: space-between;
          align-content: center;
          align-items: center;
          p {
            margin-bottom: 0;
          }
          .name {
            width: 200px;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
          }
          .time {
            font-size: 0.8em;
          }
        }
        .focus {
          background: #57b757;
          width: 20px;
          height: 100%;
          text-align: center;
          line-height: 20px;
          color: #FFF;
          border: 0;
          padding: 0;
          cursor: pointer;
          transition: all .3s;
          position: absolute;
          left: 0;
          top: 0;
          &:hover {
            background: #b75757;
          }
        }
        .message {
          flex-grow: 1;
          margin-top: 5px;
          a {
            color: #000;
          }
        }
      }
    }
  }

</style>
